AA6B;QAC7B,YAAY;QACZ,aAAa;QACb,mBAAmB;AACvB;AACA;QACI,uBAAuB;QACvB,YAAY;AAChB;AACA;QACI,aAAa;AACjB;AACA;QACI,cAAc;QACd,eAAe;QACf,gBAAgB;AACpB","sourcesContent":["<script setup lang=\"ts\">\n    import { ref, defineAsyncComponent, onBeforeMount, computed, watchEffect } from \"vue\";\n    import { container } from \"../../../../domain/core/diContainer\";\n    import { IAzureResourceGroupUrls, IBackofficeUseCase } from \"../../../../domain/useCase/IBackofficeUseCase\";\n    import { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\n    import { IRouter } from \"../../../../domain/presentation/IRouter\";\n    import { IOrganization } from \"../../../../../../common/qCloudTemp/membership\";\n    import { IDialog } from \"../../../../domain/presentation/IDialog\";\n    import { IUser_SUSI } from \"../../../../../../common/qCloudTemp/authentication\";\n    import { IApplication } from \"../../../../../../common/qCloudTemp/quickCloud\";\n\n\n\n    const router = container.resolve<IRouter>(IRouter);\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const backofficeUseCase = container.resolve<IBackofficeUseCase>(IBackofficeUseCase);\n\n    const routerParams = router.getRouteParams<{ organizationId: string }>();\n    backofficeUseCase.setBreadcrumb([\"Organizations\", \"Organization Detail\"]);\n    const organization = ref<IOrganization>();\n    const dialog = container.resolve<IDialog>(IDialog);\n    const userList = ref<Array<IUser_SUSI>>([]);\n    const appList = ref<Array<IApplication>>([]);\n    const searchUser = ref(\"\");\n    const searchApp = ref(\"\");\n    const rgUrls = ref<IAzureResourceGroupUrls>();\n\n    \n    // const currentPage = ref(1);\n    // const paginatedData = ref<Array<IUser_SUSI>>([]);\n    // const itemsPerPageOptions = ref(['10', '20', '30', 'All']);\n    // const selectedItemsPerPage = ref('10');\n\n\n    onBeforeMount(async () => {\n        listOrganizationUsers();\n        listOrganizationApps();\n        await getOrganizationInfo();\n        getResourceGroupUrls();\n    }) \n\n    async function getOrganizationInfo() {\n        await executor.execute(async () => {\n            organization.value = await backofficeUseCase.getOrganizationInfo(routerParams.organizationId);\n        }, { loading: true });  \n    }\n\n    function formatDate(date?: Date) {\n        if(date) {\n            const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n            const formattedDate = formatter.format(new Date(date));\n            return formattedDate;\n        }\n    }\n    function openManageOrganizationDialog () {\n    dialog.showDialog(\n            defineAsyncComponent(() => import(`../../components/dialogs/backoffice/manageOrganization.vue`)),\n            { closable: true, title: \"Manage Organization\", width: \"592px\", maxHeight: \"573px\" },{organization: organization.value}\n        );\n    }\n    function openDeleteOrganizationDialog () {\n    dialog.showDialog(\n            defineAsyncComponent(() => import(`../../components/dialogs/backoffice/deleteOrganization.vue`)),\n            { closable: true, title: \"Delete Organization\", width: \"577px\", maxHeight: \"206px\" },{organization: organization.value}\n        );\n    }\n    function listOrganizationUsers() {\n        const organizationId = routerParams.organizationId;\n        executor.execute(async () => {\n            userList.value = await backofficeUseCase.listOrganizationUsers(organizationId);\n        }, { loading: true });\n    }\n    function listOrganizationApps() {\n        const organizationId = routerParams.organizationId;\n        executor.execute(async () => {\n            appList.value = await backofficeUseCase.listOrganizationApps(organizationId);\n        }, { loading: true });\n    }\n    function viewUser(userId: string) {\n        router.push(`/backoffice/user/${userId}`);\n    }\n    function getFilteredUsers() {\n         return userList.value.filter((user) => (user.name?.toUpperCase().includes(searchUser.value.toUpperCase()) || \n                                                user.lastName?.toUpperCase().includes(searchUser.value.toUpperCase()) || \n                                                user.email?.toUpperCase().includes(searchUser.value.toUpperCase())) )\n    }\n    function getFilteredApps() {\n        return appList.value.filter((app) => (app.name?.toUpperCase().includes(searchApp.value.toUpperCase()) || \n                                              app.createdBy?.toUpperCase().includes(searchApp.value.toUpperCase())) )\n    }\nfunction getResourceGroupUrls() {\n    executor.execute(async () => {\n        if (!organization.value?.cloud?.azure?.resourceGroupID) {\n            console.log(`Organization '${organization.value!.id} - ${organization.value!.name}' has no \"cloud.azure.resourceGroupID\"!`)\n            return;\n        }\n        rgUrls.value = await backofficeUseCase.getAzureResourceGroupUrls(organization.value.cloud.azure.resourceGroupID!);\n    }, { loading: true });\n}\n    function openResourceGroupOverviewPage() {\n        window.open(rgUrls.value!.overviewUrl, '_blank');\n    }\n    function openResourceGroupCostPage() {\n        window.open(rgUrls.value!.costUrl, '_blank');\n    }\n    //Table Pagination\n    // const totalPages = computed(() => {\n    //     if(selectedItemsPerPage.value != 'All') {\n    //         return Math.ceil(userList.value.length / parseInt(selectedItemsPerPage.value))\n    //     }\n    //     else {\n    //         return 1;\n    //     }\n    // });\n\n    // const displayedPageNumbers = computed(() => {\n    //     const range = 2; // Number of page numbers to display on each side of the current page\n    //     let startPage = Math.max(1, currentPage.value - range);\n    //     let endPage = Math.min(totalPages.value, currentPage.value + range);\n    //     if (endPage - startPage < 2 * range) {\n    //         // Adjust page numbers to always show the same number of items\n    //         if (startPage === 1) {\n    //             endPage = Math.min(endPage + (2 * range - (endPage - startPage)), totalPages.value);\n    //         } \n    //         else if (endPage === totalPages.value) {\n    //             startPage = Math.max(startPage - (2 * range - (endPage - startPage)), 1);\n    //         }\n    //     }\n    //     let pageNumbers: Array<number> = [];\n    //     for(let i = startPage; i <= endPage - startPage + 1; i++) {\n    //         pageNumbers.push(i);\n    //     }\n    //     // return Array.from({ length: endPage - startPage + 1 }, (_, index) => startPage + index);\n    //     return pageNumbers;\n    // });\n\n    // watchEffect(() => {\n    //     if(selectedItemsPerPage.value != 'All') {\n    //         const startIndex = (currentPage.value - 1) * parseInt(selectedItemsPerPage.value);\n    //         const endIndex = startIndex + parseInt(selectedItemsPerPage.value);\n    //         const newData = userList.value.slice(startIndex, endIndex);\n    //         paginatedData.value = newData;\n    //     }\n    //     else {\n    //         paginatedData.value = userList.value;\n    //     }\n    // });\n\n    // function onItemsPerPageSelect (itemCount: string) {\n    //     selectedItemsPerPage.value = itemCount;\n    //     setCurrentPage(1)\n    // }\n    // function setCurrentPage(page: number) {\n    //     currentPage.value = page;\n    // }\n\n    // watchEffect(() => {\n    //     currentPage.value = 1;\n    // });\n    ////////////////////////////////////////////////////\n</script>\n\n<template>\n    <div class=\"manage-org-div\">\n        <button class=\"manage-btn\" @click=\"openManageOrganizationDialog()\"><i class=\"mdi mdi-cog\" style=\"font-size: 20px; margin-right: 4px\"></i><span>MANAGE ORGANIZATION</span></button>\n        <button class=\"delete-org-btn\" @click=\"openDeleteOrganizationDialog()\"><i class=\"mdi mdi-delete\" style=\"font-size: 20px; margin-right: 4px\"></i><span>DELETE</span></button>\n    </div>\n    <div style=\"display:flex; margin-bottom: 32px;\">\n        <div class=\"organization-info-type\">\n            <div class=\"organization-info-type-name\">Name</div>\n            <div class=\"organization-info\">{{ organization?.name }}</div>\n        </div>\n        <div class=\"organization-info-type\">\n            <div class=\"organization-info-type-name\">Users</div>\n            <div class=\"organization-info\">{{ userList.length }}</div>\n        </div>\n        <div class=\"organization-info-type\">\n            <div class=\"organization-info-type-name\">Apps</div>\n            <div class=\"organization-info\">{{ appList.length }}</div>\n        </div>\n        <div class=\"organization-info-type\" style=\"flex: 1;\">\n            <div class=\"organization-info-type-name\">Date Created</div>\n            <div class=\"organization-info\">{{ formatDate(organization?.createDate) }}</div>\n        </div>\n        <div class=\"organization-info-type\" style=\"flex: 1;\">\n            <div class=\"organization-info-type-name\">ID</div>\n            <div class=\"organization-info\" style=\"font-size: 20px;\">{{ organization?.id }}</div>\n        </div>\n        <div class=\"organization-info-type\">\n            <div class=\"organization-info-type-name\">DB Name</div>\n            <div class=\"organization-info\">{{ organization?.dbName }}</div>\n        </div>\n    </div>\n    <div style=\"margin-bottom: 32px;\">\n        <div class=\"tags-div\">\n            <span class=\"tags-title\">Tags</span>\n        </div>\n    </div>\n    <div style=\"margin-bottom: 32px;\">\n        <div class=\"div-title\">Users ({{ getFilteredUsers().length }})</div>\n        <div class=\"filter-area\">\n            <div class=\"search-in-table\">\n                <i class=\"mdi mdi-magnify\"></i>\n                <input class=\"search-input\" type=\"text\" placeholder=\"Search\" alt=\"search\" v-model=\"searchUser\" @input=\"getFilteredUsers()\"/>\n            </div>\n        </div>\n        <div>\n            <table class=\"user-app-list\">\n                <tr class=\"thead\">\n                    <th class=\"col-xl-3\">ID</th>\n                    <th class=\"col-xl-3\">Object ID</th>\n                    <th class=\"col-xl-1\">Name</th>\n                    <th class=\"col-xl-1\">Last Name</th>\n                    <th class=\"col-xl-1\">E-mail</th>\n                    <th class=\"col-xl-1\">Owner</th>\n                    <th class=\"col-xl-1\">Activity</th>\n                    <th class=\"col-xl-1\"></th>\n                </tr>\n                <tr v-for=\"user in getFilteredUsers()\" :key=\"user.id\">\n                    <td>{{ user.id }}</td>\n                    <td>{{ user.objectId }}</td>\n                    <td>{{ user.name }}</td>\n                    <td>{{ user.lastName }}</td>\n                    <td>{{ user.email }}</td>\n                    <td> {{ organization?.creatorUserId == user.id ? 'Owner': 'Member' }} </td>\n                    <td>  \n                        <span style=\"background: #FF6B6B; border-radius: 50%;padding: 6px 10px;\">0</span> \n                        <!-- <span style=\"border-radius: 50%;padding: 6px 10px;\" \n                        :style=\"{background: user.activity == 0 ? '#FF6B6B' : user.activity >= 1 && user.activity <= 3 ? '#F3CA40' : user.activity >=4 ? '#29C660' : ''}\">\n                        {{ user.activity }}</span>  -->\n                    </td>\n                    <td><button class=\"view-btn\" @click=\"viewUser(user.id)\">VIEW</button></td>\n                </tr>\n                <!-- <tr v-show=\"userList.length > 10\" style=\"display: none\">\n                    <td colspan=\"1\" style=\"display: flex; align-items: center;\">\n                        <span style=\"font-size: 14px; color: #616161\">Items per page:</span>\n                        <CustomSelect :options=\"itemsPerPageOptions\" @input=\"onItemsPerPageSelect\" class=\"items-per-page\" :selected-class=\"'item-count'\" :default=\"selectedItemsPerPage\"></CustomSelect>\n                    </td>\n                    <td colspan=\"7\">\n                        <div style=\"float: right; display: flex; align-items: center;\">\n                            <button class=\"change-page-number\" @click=\"currentPage--\" :disabled=\"currentPage === 1\">\n                                <i class=\"mdi mdi-chevron-left\" :style=\"{color: currentPage === 1 ? '#BCBCBC' : ''}\"></i>\n                            </button>\n                            <button v-for=\"pageNumber in displayedPageNumbers\" :key=\"pageNumber\" @click=\"setCurrentPage(pageNumber)\" :class=\"currentPage === pageNumber ? 'page-number active' : 'page-number'\"> {{ pageNumber }} </button>\n                            <button class=\"change-page-number\" @click=\"currentPage++\" :disabled=\"currentPage === totalPages\">\n                                <i class=\"mdi mdi-chevron-right\" :style=\"{color: currentPage === totalPages ? '#BCBCBC' : ''}\"></i>\n                            </button>\n                        </div>\n                    </td>\n                </tr> -->\n                <tr style=\"display:none\"></tr>\n            </table>\n        </div>\n    </div>\n    <div style=\"margin-bottom: 32px;\">\n        <div class=\"div-title\">Application List ({{ appList.length }})</div>\n        <div class=\"filter-area\">\n            <div class=\"search-in-table\">\n                <i class=\"mdi mdi-magnify\"></i>\n                <input class=\"search-input\" type=\"text\" placeholder=\"Search\" alt=\"search\" v-model=\"searchApp\" @input=\"getFilteredApps()\"/>\n            </div>\n        </div>\n        <div>\n            <table class=\"user-app-list\">\n                <tr class=\"thead\">\n                    <th class=\"col-xl-2\">ID</th>\n                    <th class=\"col-xl-2\">Name</th>\n